import { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import SockJsClient from "react-stomp";
import { base_url } from "../config/urls";
import { setLobbyDetails, setUsers } from "../Redux/lobby.slice";
import { setMessage } from "../Redux/message.slice";
import { setCurrentUser, setRecentCard } from "../Redux/table.slice";

function ServerConnection({ setCurrentPage }) {
  let code = useSelector((state) => state?.lobby?.code);
  let userId = useSelector((state) => state?.user?.id);
  let messageCount = useSelector((state) => state?.message?.count);
  let message = useSelector((state) => state?.message?.message);
  const ref = useRef();

  const dispatch = useDispatch();

  let messageProcessor = {
    JOIN_LOBBY: ({ requestBody }) => {
      dispatch(setUsers(requestBody?.listOfConnectedUsers ?? []));
      dispatch(setLobbyDetails(requestBody));
      setCurrentPage(2);
    },
    CARD_DISTRIBUTE: ({ requestBody }) => {
      console.log(requestBody);
      dispatch(setCurrentUser(requestBody.badamSattiOwnerId));
      setCurrentPage(3);
    },
    FIND_NEXT_PLAYER: ({ requestBody }) => {
      dispatch(setRecentCard(requestBody?.cardPlayed));
      dispatch(setCurrentUser(requestBody.currentUserId));
    },
  };
  const [conn, setConn] = useState(false);

  useEffect(() => {
    console.log(message, conn);
    if (conn && Object.keys(message).length) {
      ref?.current?.sendMessage(
        "/app/badamsatti/getmessage",
        JSON.stringify(message)
      );
    }
  }, [conn, messageCount]);
  return (
    <SockJsClient
      url={`${base_url}/connect`}
      topics={["/subscribe/get-subscription/" + code]}
      onMessage={(msg) => {
        console.log(msg);
        ref?.current?.sendMessage("/app/badamsatti/getmessage");
        messageProcessor[msg.msg](msg);
      }}
      onConnect={() => setConn(true)}
      ref={ref}
    />
  );
}

export default ServerConnection;
