import { createSlice } from "@reduxjs/toolkit";

export const tableSlice = createSlice({
  name: "table",
  initialState: {
    cards: [],
    currentUser: null,
    currentCard: null,
    cardPlayable: false,
    cardPlayDetails: {},
    recentCards: [
      { min: [], max: [] },
      { min: [], max: [] },
      { min: [], max: [] },
      { min: [], max: [] },
    ],
  },
  reducers: {
    setCards: (state, action) => {
      state.cards = action.payload;
      return state;
    },
    setCurrentUser: (state, action) => {
      state.currentUser = action.payload;
      return state;
    },
    setCurrentCard: (state, action) => {
      state.currentCard = action.payload;
      if(action.payload !== 7 && state.recentCards[0].min.length === 0) {
        state.cardPlayable = false;
        return state;
      }
      let setIndex = parseInt(action.payload / 13);
      if (action.payload % 13 === 0) {
        setIndex -= 1;
      }
      let minMaxCard = "min",
        func = (k) => k - 1 === action.payload;

      if (action.payload % 13 === 7) {
        state.cardPlayable = true;
        state.cardPlayDetails = {
          setIndex,
          minMaxCard: "",
          index: null,
        };
        return state;
      } else if (action.payload % 13 > 7 || action.payload % 13 === 0) {
        minMaxCard = "max";
        func = (k) => k + 1 === action.payload;
      }
      let index =
        state?.recentCards?.[setIndex]?.[minMaxCard]?.findIndex(func) ?? -1;
      state.cardPlayDetails = {
        setIndex,
        minMaxCard,
        index,
      };
      state.cardPlayable = index !== -1;
      return state;
    },
    setRecentCard: (state, action) => {
      let { setIndex, minMaxCard, index } = state.cardPlayDetails;
      console.log(setIndex, minMaxCard, index);
      if (!minMaxCard) {
        state.recentCards[setIndex].min.push(action.payload);
        state.recentCards[setIndex].max.push(action.payload);
      } else {
        state.recentCards[setIndex][minMaxCard][index] = action.payload;
      }
      state.cards.splice(
        state.cards.findIndex((k) => k === action.payload),
        1
      );
      return state;
    },
  },
});

// Action creators are generated for each case reducer function
export const { setRecentCard, setCards, setCurrentUser, setCurrentCard } =
  tableSlice.actions;

export default tableSlice.reducer;
